
/* top level */
html {
  background: radial-gradient(circle, rgb(252, 255, 217) 0%, rgb(6, 244, 215) 0%, rgb(249, 253, 216) 100%);
}

.garden-planner > h2:nth-child(1) {
  display: none;
}

.garden-planner > header{
  display: flex;
  justify-content: center;
  margin-top:5vh;
}

.logo {
  align-content: center;
}

/* secondary level */
.app-content{
  margin: 5vw;
  background: white;
  border-radius: 30px;
  margin-top:5vh;
}

/* layout grid for content */
.app-grid {
  display: grid;
  grid-template-areas:
    "top-left mode top-right"
    "left grid vegetables"
    "left configHandling right"
    "debug debug debug";
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-rows: auto 60vh auto auto auto;
}

/* main grid sections */

.top-left {
  grid-area: top-left;
  text-align: center;
}

.left {
  grid-area: left;
}

.right {
  grid-area: right;
}

.debug {
  grid-area: debug;
}

/* garden grid section */
.grid {
  grid-area: grid;
  display: grid;
  grid-template-areas:
    "gridleft gridmiddle gridright";
  grid-template-columns: auto auto auto;
  overflow: hidden;
  width: 80vw;
  height: 60vh;
  z-index: 1;
  box-shadow: 0px 0px 11px -1px gray;
  border-radius: 30px;
  position: relative;
}

.help {
  position: relative;
  z-index: 2235;
  cursor: help;
  background: #d6deff;
  border-radius: 50%;
  height: 50px;
  width: 50px;
  text-align: center;
  margin: auto;
  line-height: 46px;
  font-weight: bold;
  font-size: xx-large;
  grid-area: left;
  left: 0vw;
  bottom: 11vh;
}

/* Tooltip styling using a pseudo-element */
.help:hover::after {
  content: "This is the help tooltip!";
  position: absolute;
  bottom: 120%;  /* position above the help icon */
  left: 50%;
  transform: translateX(-50%);
  background: #333;
  color: #fff;
  padding: 5px 8px;
  border-radius: 4px;
  white-space: nowrap;
  opacity: 1;
  pointer-events: none;
  z-index: 5000;
}

/* Grid container wrapper styling */
.grid-container-wrapper {
  margin-right: 0px !important;
  position: relative;
  overflow: hidden;
  background: #ffffff;
  overscroll-behavior: contain;
}

.grid-container {
  position: relative;
  background: #ffffff;
  border: 2px solid;
  top: -30px;
  left: -30px;
}

/* Grid cell styling */
.grid-cell {
  width: 100px;
  height: 100px;
  border: 1px solid #aaa;
  box-sizing: border-box;
  float: left;
  position: relative;
}

.grid-cell.selected {
  background-color: lightgreen;
}

.grid-cell.select-mode {
  background-color:#fff0d8;
  border: none;
}

.grid-cell.drag-over-not-selected {
  background-color: red;
}

.grid-cell.drag-over-selected {
  background-color: green !important;
}

.grid-cell.selected.select-mode {
  background-color: lightgreen;
  border: 1px solid #aaa;
}

/* Container for dropped vegetable images */
.cell-content {
  width: 100%;
  height: 100%;
}

/* Images in cell content */
.cell-content img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}



/* Selection overlay styling */
.current-selection.add {
  position: absolute;
  border: 2px dashed green;
  background-color: rgba(0, 255, 0, 0.2);
  pointer-events: none;
  z-index: 10;
}

.current-selection.remove {
  position: absolute;
  border: 2px dashed red;
  background-color: rgba(255, 0, 0, 0.2);
  pointer-events: none;
  z-index: 10;
}

/* Vegetable list styling */

.vegetables {
  grid-area: gridright;
  border-left: none;
  z-index: 1;
  box-shadow: -9px 0px 6px -6px rgba(0,0,0,0.75);
}

.vegetable-list {
  top: 0;
  right: 0;
  z-index: 1;
  background-color: white;
  display: flex;
  flex-direction: column;
  height: 60vh;
  margin: 2px 20px 2px 20px;
}

.vegetable-search {
  width: 100%;
  padding: 5px;
  box-sizing: border-box;
  margin-bottom: 5px;
  background: none;
  margin-top: 10px;
  border:none;
  border-bottom: 2px solid #0034ff29;
}

.vegetable-list-inner {
  overflow-y: auto;
  flex: 1;
}

.vegetable-item {
  display: flex;
  align-items: center;
  cursor: grab;
  margin-bottom: 5px;
  margin-right: 80px;
}

.vegetable-item:hover {
  background-color: rgba(0,0,0,0.2);
}

.vegetable-item img {
  width: 50px;
  height: 50px;
  margin-right: 5px;
}

/* Mode selector styling */

.mode {
  grid-area: mode;
  text-align: center;
  border-radius: 10px 10px 0px 0px;
  z-index: 1;
}

.tabs {
  margin-bottom: 30px;
}

nav {
  position: relative;
  z-index: 1;

  & > a {
    position: relative;
    width: 10em;
    display: inline-block;
    padding: .7em 2em .7em;
    color: black;
    text-decoration: none;
  }

  a::before {
    content: '';
    position: absolute;
    top: 0; right: 0; bottom: 0; left: 0;
    z-index: -1;
    border-bottom: none;
  }
}

nav a.active {
  z-index: 2;
  color: #2000ff;
  font-weight: bold;
  background: #0034ff29;
  border-radius: 5px;
  margin: 15px;
  &::before {
    color: black;
  }
}

nav a.active::before {
  border-radius: 5px;
}


/* Dropdown container styling */
.dropdown-container {
  font-size: 14px;
  z-index: 1;
}

.dropdown-group {
  margin-bottom: 5px;
}

.dropdown-group-title {
  font-weight: bold;
  margin-bottom: 2px;
  text-align: center;
  text-decoration: underline;  
}

.dropdown-group-title:hover {
  cursor:default;
}

.dropdown-item {
  cursor: pointer;
  padding: 2px 0;
}

.dropdown-item:hover {
  cursor: pointer;
  padding: 2px 0;
  background-color: rgba(0,0,0,0.2);
}
.dropdown-spacer {
  padding-top: 10px;
}

/* Config handling styling */
.configHandling {
  margin-top: 15vh;
  grid-area: configHandling;
  box-shadow: 0px 0px 11px -1px gray;
  border-radius: 30px;
}

.manageConfig {
  margin: 30px;
}

.manageConfig > button {
  z-index: 2;
  color: #2000ff;
  font-weight: bold;
  background: #0034ff29;
  border-radius: 5px;
  margin-right: 20px;
  margin-top: 20px;
  padding: 15px;
  border: none;
  cursor: pointer;
}

.manageConfig > button:nth-child(4) {
  z-index: 2;
  color: #2000ff;
  font-weight: bold;
  background: none;
  border-radius: 5px;
  margin-right: 20px;
  margin-top: 20px;
  padding: 15px;
  border: none;
  cursor: pointer;
  }

